/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html,
button,
input,
select,
textarea {
    color: #222;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

img {
    vertical-align: middle;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

textarea {
    resize: vertical;
}

.chromeframe {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}
@font-face {
  font-family: "PT Sans";
  src: url(/bill-of-entry/static/media/PTSans-Regular.334e1813.ttf)
}
@font-face {
  font-family: "Proxima Nova";
  src: url(/bill-of-entry/static/media/ProximaNova-Regular.b078618b.ttf)
}
@font-face {
  font-family: "Proxima Nova Bold";
  src: url(/bill-of-entry/static/media/ProximaNova-Bold.7722451a.ttf)
}
@font-face {
  font-family: "Proxima Nova Thin";
  src: url(/bill-of-entry/static/media/ProximaNova-Light.f70705f2.ttf)
}
@media (max-width: 1200px) {
  .form-bill__header.m-form-header {
    font: normal 600 24px/29px "Proxima Nova";
    display: flex;
    align-items: center;
    text-align: center;
  }
  .form-bill__text.m-form-text {
    font: 18px/22px "Proxima Nova";
    text-align: center;
  }
  .form-bill__wrap--reg.m-form-bill__wrap--reg {
    width: calc(100% - 40px);
  }
  .form-bill__wrap--confirm.m-form-bill__wrap--confirm {
    max-width: 289px;
    width: 100%;
  }
  .form-bill__wrap--active.m-form-bill__wrap--active {
    max-width: 374px;
    width: 100%;
  }
  .header {
    height: 76px;
    overflow: hidden;
    transition: height .5s linear;
  }
  .header.header--active {
    height: var(--compact-menu-height);
    top: 0;
    transition: height .5s linear;
  }
  .m-menu.menu {
    justify-content: flex-start;
    flex-direction: column;
  }
  .menu__logo {
    display: none;
  }
  .menu__compact {
    width: 20px;
    height: 12px;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAMCAYAAABiDJ37AAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAApSURBVHgB7dFBAQAABATBJYn+pWhyQvDwMBXGJCUQ7CjnTdmn3HM/pQFwEA6eL9aipwAAAABJRU5ErkJggg==);
    list-style: none;
    display: flex;
    justify-content: flex-start;
    margin: 33px 15px 31px auto;
  }
  .menu__item {
    border-top: 1px solid #fff;
  }
}
.header {
  background: #0F7A65;
  width: 100%;
  position: fixed;
  top: 0;
}
.menu__item {
  padding: 33px 30px 34px;
  font: 18px/23px "PT Sans";
  display: flex;
  align-items: center;
}
.menu__logo {
  box-sizing: content-box;
  margin: 10px 0 8px 30px;
  width: 59px;
  height: 72px;
  background: url(data:image/png;base64,);
  list-style: none;
  flex-shrink: 0;
}
.menu {
  display: flex;
  justify-content: center;
  list-style: none;
}
.menu__link {
  color: #FFFFFF;
  text-decoration: none;
}
.menu__link:hover {
  text-decoration: underline;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.form-bill {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  margin: 0 20px;
}
.form-bill__logo {
  width: 120px;
  height: 120px;
  border-radius: 60px;
  background: #25B887;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}
.form-bill__logo--letter::before {  
  width: 66px;
  height: 66px;
  background: url(data:image/png;base64,)
}
.form-bill__logo--activate::before {
  width: 78px;
  height: 56px;
  background: url(data:image/png;base64,)
}
.form-bill__logo::before {
  display: block;
  content: "";
}
.form-bill__header {
  font: bold 36px/44px "Proxima Nova Thin";
  display: flex;
  align-items: center;
  text-align: center;
  color: #000000;
  margin-bottom: 10px;
}
.form-bill__text {
  font: 24px/29px "Proxima Nova";
  text-align: center;
  color: #272727;
  margin-bottom: 50px;
}
.form-bill__wrap--reg {
  width: 350px;
}
.form-bill__wrap--confirm {
  width: 260px;
}
.form-bill__wrap--active {
  width: 465px;
}
.form-bill__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form-bill__email {
  height: 50px;
  width: 100%;
  border: 1px solid #9B9B9B;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 14px 15px;
  margin-bottom: 20px;
  font: 18px/22px "Proxima Nova";
  color: #9B9B9B;
}
.form-bill__send {
  height: 50px;
  background: #25B887;
  border-radius: 5px;
  border: 1px solid #25B887;
  width: 100%;
  font: 600 18px/22px "Proxima Nova Thin";
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 25px;
}
.form-bill__link {
  font: 18px/22px "Proxima Nova Thin";
  text-align: center;
  text-decoration: none;
  color: #272727;
}
.form-bill__number {
  width: 50px;
  height: 50px;
  text-align: center;
  background: #FFFFFF;
  border: 1px solid #9B9B9B;
  box-sizing: border-box;
  border-radius: 5px;
  margin: 0 0 20px 0;
  font: 18px/22px "Proxima Nova";
  color: #9B9B9B;
}
.form-bill__number:last-child {
  margin: 0;
}
.form-bill__label {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.form-bill__email-label {
  width: 100%;
}
.form-bill__other-auth {
  display: flex;
  justify-content: center;
}
.text-confirm {
  max-width: 465px;
  margin: 0 20px 50px;
}
.blackout {
  background: rgba(0,0,0,.45);
  height: 100vh;
  width: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-window {
  margin: 0 15px;
  max-width: 600px;
  min-width: 150px;
  background: #fff;
  border-radius: 5px;
}
.modal-window__header {
  font: 600 24px "Proxima Nova";
  display: flex;
  align-items: center;
  text-align: center;
  padding: 25px 0 15px;
  margin: 0 25px;
  border-bottom: 1px solid #9B9B9B;
}
.modal-window__text {
  font: 18px/22px "Proxima Nova";
  margin: 15px 25px;
}
.modal-window__btn {
  height: 50px;
  background: #25B887;
  border-radius: 5px;
  border: 1px solid #25B887;
  font: 600 18px/22px "Proxima Nova Thin";
  text-align: center;
  color: #FFFFFF;
  margin: 0 25px 25px;
  width: calc(100% - 50px);
}
